import { addTable, county } from './json'
import { dataFormat } from '../../frame_template/js/utils'
const COLORS = ['#fee9e9', '#e1f5fe', '#e8f6ed'];
export default {
  data() {
    return {
    	editFlag: 0,
      value: '',
      projectStage: '', //项目阶段
      options: [],
      tableData: [],
      editId: sessionStorage.getItem("EditCityProjectID"),
      county: county,
      forms: {
      	name: '', //项目名称
      	startTime: '', //开工年份
      	endTime: '', //竣工年份
      	finishTime: '', //截止申报时间
      	contacts: '', //总负责人
      	invest: '', //规划总投资
      	procedureType: '', //程序流程id
      	projectData: '', //项目阶段、进度数据
      	townData: '' //区县范围数据
      },
      townList: [], //区县范围列表
      checkTownList: [], //选中的区县范围列表
      projectTownList: [],
      procedureList: []
    }
  },
  
  // 页面初始化
	created: function() {
			this.findProcedureList();
	},
  
  mounted() {
    this.$nextTick(() => {
    	if(!!this.editId) {
    		this.queryProjectById();
    	}
    	this.queryTownInfo(this.editId);
      this.getSpanArr(addTable);
      this.tableData = JSON.parse(JSON.stringify(addTable));
      if(!!!this.editId) { //新增
      	this.tableData.forEach((data, index) => {
      		data.selectValue = '';
    			data.flowPath = [];
    		})
      }
    })
  },

  methods: {
  	queryProjectById() {
  		this.$post('/admin/queryProjectById', {'id': this.editId}).then((m) => {
  			let obj = m.content.tProject;
  			let tProjectStageList = m.content.tProjectStageList;
  			this.editFlag = m.content.editFlag;
  			//组装前台数据
  			let newArrays = [];
  			tProjectStageList.forEach((tProjectStage, index) => {
  				let tableObj = {};
  				tableObj.id = tProjectStage.id;
  				tableObj.type = index++;
  				tableObj.name = tProjectStage.name;
  				tableObj.isDao = tProjectStage.isDaopai;
  				tableObj.plan = tProjectStage.plan;
  				tableObj.selectValue = tProjectStage.selectValue;
  				tableObj.flowPath = [];
  				newArrays.push(tableObj);
  			})
				this.getSpanArr(newArrays);
  			this.forms = obj;
  			this.tableData = newArrays;
				this.changeProcedure(obj.procedureType);
  		})
  	},
    //合并单元格统计
    getSpanArr(data) {
      this._spanArr = [];
      for (var i = 0; i < data.length; i++) {
        if (i === 0) {
          this._spanArr.push(1);
          this._pos = 0;
        } else {
          // 判断当前元素与上一个元素是否相同
          if (data[i].id === data[i - 1].id) {
            this._spanArr[this._pos] += 1;
            this._spanArr.push(0);
          } else {
            this._spanArr.push(1);
            this._pos = i;
          }
        }
      }
    },

    //合并单元格
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      if ( columnIndex === 0 ) {
        const _row = this._spanArr[rowIndex];
        const _col = _row > 0 ? 1 : 0;
        return {
          rowspan: _row,
          colspan: _col
        };
      }

      if( columnIndex === 2 && (row.isAdd || row.isEdit) ) {
        return {
          rowspan: 1,
          colspan: 0
        };
      }

      if( columnIndex === 3 && (row.isAdd || row.isEdit) ) {
        return {
          rowspan: 1,
          colspan: 2
        };
      }
    },
    
    //设置列样式
    setCellStyle({row, column, rowIndex, columnIndex}) {
      if( columnIndex === 0 ) {
        this._index === undefined ? this._index = 0 : this._index++;
        return {
          backgroundColor: COLORS[this._index % 3],
          fontSize: '18px',
        }
      }else {
        return {
          color: '#606266'
        }
      }
    },

    //添加进度
    addPlan(row, index) {
      if( row.plan === '' ) {
        return this.$message({
          showClose: true,
          message: '请输入进度名称！',
          type: 'error'
        });
      }

      if( !!row.isEdit ) return row.isEdit = false;

      let obj = JSON.parse(JSON.stringify(row))
      obj.isAdd = false;
      this.tableData.splice(index, 0, obj)
      this.getSpanArr(this.tableData);
      row.plan = "";
      row.selectValue = null;
      row.flowPath = this.procedureList;
    },

    // 删除进度
    delPlan(row, index) {
      this.tableData.splice(index, 1);
      this.getSpanArr(this.tableData);
    },

    planEdit(row) {
      if(!!row.isEdit) return;
      
      if( typeof row.isEdit === 'undefined' ) {
      	this.$set(row, 'isEdit', true);
      }else {
      	row.isEdit = true;
      }
    },

    //进度排序
    planSort(row, index, sort) {
      if( sort === 'up' ) {
        let f = index - 1 >= 0
              && this.tableData[index].id !== this.tableData[index-1].id;
        if( f || index === 0 ) return;

        this.tableData.splice(index, 1);
        this.tableData.splice(index-1, 0, row);

      }else {
        let f = this.tableData.length > index + 1 
              && !!this.tableData[index+1].isAdd;
        if( f ) return;

        this.tableData.splice(index, 1);
        this.tableData.splice(index+1, 0, row);
      }
    },

    closeEdit(row) {
      if( !!row.isEdit ) {
        row.isEdit = false;
      }else {
        row.plan = '';
        row.selectValue = '';
      }
    },

    handlerCheckbox(type) {
      //全选
      if( type === 1 ) {
        this.townList.forEach(item => {
        	if( typeof item.isChecked === 'undefined' ) {
        		this.$set(item, 'isChecked', true);
        	}else{
        		item.isChecked = true;
        	}
        })
      }

      //取消
      if( type === 2 ) {
        this.townList.forEach(item => {
        	if( typeof item.isChecked === 'undefined' ) {
        		this.$set(item, 'isChecked', false);
        	}else{
        		item.isChecked = false;
        	}
        })
      }

      //反选
      if( type === 3 ) {
        this.townList.forEach(item => {
        	if( typeof item.isChecked === 'undefined' ) {
        		this.$set(item, 'isChecked', true);
        	}else{
        		item.isChecked = !item.isChecked;
        	}
        })
      }
    },
    changeProcedure(data) {
    	this.$post('/admin/queryAllProcedureById', {'id': data}).then((m) => {
    		let procedureList = m.content.procedureList;
    		this.procedureList = [];
    		procedureList.forEach((item, index) => {
    			let obj = {};
    			if(!!item.name && !!item.aliasName) {
    				obj.label = item.aliasName;
    			}else if(!!item.name) {
    				obj.label = item.name;
    			}else if(!!item.aliasName) {
    				obj.label = item.aliasName;
    			}
  				obj.id = item.id;
  				this.procedureList.push(obj);
    		})
    		this.tableData.forEach((data1, index) => {
    			data1.flowPath = this.procedureList;
    		})
    	})
    },
    //保存项目阶段
    saveProjectStage() {
    	if(!!!this.projectStage) {
    		return this.$message({
    			showClose: true,
					type: 'error',
					message: '请输入阶段名称！'
				});
    	}else if(!!!this.forms.procedureType){
    		return this.$message({
    			showClose: true,
					type: 'error',
					message: '请先选择主程序流程！'
				});
    	}else if(this.tableData.some(c => c.name == this.projectStage)){
    		return this.$message({
    			showClose: true,
					type: 'error',
					message: '阶段名重复！'
				});
    	}else{
    		let time = new Date().getTime()+'';
    		let obj = {};
    		obj.id = time.substring(10);
    		obj.type = 4;
    		obj.name = this.projectStage;
    		obj.plan = "";
    		obj.isDao = 1;
    		obj.isAdd = true;
    		obj.flowPath = this.procedureList;
    		this.tableData.splice(this.tableData.length, 0, obj);
    		this.getSpanArr(this.tableData);
    		this.projectStage = "";
    	}
    },
    //
    queryTownInfo(id) {
    	this.$post('/web/queryTownInfo', {"projectId": id}).then((m) => {
    		this.townList = m.content.tDictionaryList;
    		this.projectTownList = m.content.tProjectTownList;
    		this.townList.forEach((item, index) => {
    			this.$set(item, 'danwei', '');
    			this.$set(item, 'lingdao', '');
    			this.$set(item, 'isChecked', false);
    			this.projectTownList.forEach((town) => {
    				if(town.townId == item.code) {
    					item.isChecked = true;
    					if(!!town.accountabilityUnit) {
	    					item.danwei = town.accountabilityUnit;
	    				}
	    				if(!!town.contacts) {
	    					item.lingdao = town.contacts;
	    				}
    				}
    			})
    			
    		})
			})
    },
    //查询程序流程类型列表
    findProcedureList() {
    	this.$post('/web/findProcedureList').then((m) => {
    		this.options = m.content.tProcedureTypeList;
    	})
    },
    //验证开工时间
    validStartTime(data) {
    	if(!!this.forms.endTime && !!data) {
    		if(this.forms.endTime < data.getTime()) {
    			this.$message({
	    			showClose: true,
						type: 'warning',
						message: '竣工时间应大于开工时间！'
					});
					this.forms.endTime = '';
					return false;
    		}
    	}
    },
    //验证竣工时间
    validEndTime(data) {
    	if(!!this.forms.startTime && !!data) {
    		if(this.forms.startTime > data.getTime()) {
    			this.$message({
	    			showClose: true,
						type: 'warning',
						message: '开工时间应小于竣工时间！'
					});
					this.forms.startTime = '';
					return false;
    		}
    	}
    },
    changeStageName(row) {
    	this.tableData.forEach((item) => {
    		if(item.id == row.id) {
    			item.name = row.name;
    		}
    	})
    },
    //保存
    finalSave() {
    	if(!!!this.forms.name) {
    		this.$message({
    			showClose: true,
					type: 'error',
					message: '请输入项目名称！'
				});
				return false;
    	}
    	if(!!!this.forms.startTime) {
    		this.$message({
    			showClose: true,
					type: 'error',
					message: '请选择开工日期！'
				});
				return false;
    	}
    	if(!!!this.forms.endTime) {
    		this.$message({
    			showClose: true,
					type: 'error',
					message: '请选择竣工日期！'
				});
				return false;
    	}
    	if(!!!this.forms.contacts) {
    		this.$message({
    			showClose: true,
					type: 'error',
					message: '请输入总负责人！'
				});
				return false;
    	}
    	if(!!!this.forms.invest) {
    		this.$message({
    			showClose: true,
					type: 'error',
					message: '请输入规划总投资！'
				});
				return false;
    	}
    	let regf = /^(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*))$/;
    	if(!regf.test(this.forms.invest)) {
    		this.$message({
    			showClose: true,
					type: 'error',
					message: '规划总投资只能为数字格式！'
				});
				return false;
    	}
    	if(!!!this.forms.procedureType) {
    		this.$message({
    			showClose: true,
					type: 'error',
					message: '请选择主程序流程！'
				});
				return false;
    	}
    	//阶段 进度 操作流程
    	this.forms.projectData = this.tableData;
    	this.forms.startTime = dataFormat(new Date(this.forms.startTime), 'YYYY-MM-DD HH:mm:ss');
    	this.forms.endTime = dataFormat(new Date(this.forms.endTime), 'YYYY-MM-DD HH:mm:ss');
    	if(!!this.forms.finishTime) {
    		this.forms.finishTime = dataFormat(new Date(this.forms.finishTime), 'YYYY-MM-DD HH:mm:ss');
    	}
    	if(!!!this.forms.finishTime) {
    		delete this.forms.finishTime
    	}
    	for (var i=0; i < this.townList.length; i++) {
    		if(this.townList[i].isChecked) {
    			if(!!!this.townList[i].danwei) {
    				this.$message({
		    			showClose: true,
							type: 'error',
							message: '请输入责任单位！'
						});
						return false;
    			}
    			if(!!!this.townList[i].lingdao) {
    				this.$message({
		    			showClose: true,
							type: 'error',
							message: '请输入定点联系领导！'
						});
						return false;
    			}
    			delete this.townList[i].townType;
    			this.checkTownList.push(this.townList[i]);
    		}
    	}
    	for(let k=0; k<this.tableData.length; k++) {
    		let itemTable = this.tableData[k];
    		if(!itemTable.isAdd && !itemTable.selectValue) {
    			this.$message({
	    			showClose: true,
						type: 'error',
						message: `请选择 ${itemTable.plan} 程序流程！`
					});
					return false;
    		}
    	}
    	if(this.checkTownList.length < 1) {
    		this.$message({
	    			showClose: true,
						type: 'error',
						message: '请选择区县范围！'
					});
					return false;
    	}
    	this.forms.townData = this.checkTownList;
    	let str = this.forms;
    	this.$post('/admin/createProject', {'project': JSON.stringify(this.forms)}).then((m) => {
    		if(m.resultCode == 0) {
    			this.$message({
	    			showClose: true,
						type: 'success',
						message: '操作成功！'
					});
					this.tableData = [];
					this.$router.push({ name: 'CityProject'});
    		}else if(m.resultCode == -1) {
    			this.$message({
	    			showClose: true,
						type: 'error',
						message: m.resultDesc
					});
    		}
			})
    },
    cancelSave() {
    	this.tableData = [];
    	this.$router.push({path: '/CityProject'});
    }
  }
}



